import React, { useState } from 'react';
import { Form } from 'react-final-form';
import * as Yup from 'yup';
import { Button } from '@ui/button';
import InputField from '@components/fields/input-field';
import SelectField from '@components/fields/select-field';
import { validateFormValues } from '@lib/validateFormValues';

const validationSchema = Yup.object().shape({
  lastName: Yup.string().required('    '),
  firstName: Yup.string().required('    '),
  hasMiddleName: Yup.bool(),
  middleName: Yup.string().when('hasMiddleName', {
    is: true,
    then: (schema) => schema.required('    '),
    otherwise: (schema) => schema.notRequired(),
  }),
  hasTaxId: Yup.bool(),
  taxId: Yup.string().when('hasTaxId', {
    is: true,
    then: (schema) =>
      schema
        .required('    ')
        .length(10, '    10 '),
    otherwise: (schema) => schema.notRequired(),
  }),
  dob: Yup.string().required('    '),
  gender: Yup.string().required('    '),
  birthCountry: Yup.string().required('    '),
  birthPlace: Yup.string().required('    '),
  contactMethod: Yup.string().required('    '),
  secretWord: Yup.string()
    .min(6, '      6 ')
    .required('    '),
  phone: Yup.string().matches(
    /^\+38\s?\(?0\d{2}\)?\s?\d{3}-?\d{2}-?\d{2}$/,
    '  . : +38 (093) 999-88-77'
  ),
  email: Yup.string().email('  email'),
  docType: Yup.string().required('    '),
  docNumber: Yup.string()
    .matches(
      /^[--]{3}\d{5,9}$/,
      '   3     5  9 '
    )
    .required('    '),
  docIssueDate: Yup.string().required('    '),
  docIssuer: Yup.string().required('    '),
  unrzNumber: Yup.string(),
});

interface PatientFormValues {
  lastName: string;
  firstName: string;
  hasMiddleName: boolean;
  middleName: string;
  hasTaxId: boolean;
  taxId: string;
  dob: string;
  gender: string;
  birthCountry: string;
  birthPlace: string;
  contactMethod: string;
  phone: string;
  secretWord: string;
  email: string;
  docType: string;
  docNumber: string;
  docIssueDate: string;
  docExpiryDate: string;
  docIssuer: string;
  unrzNumber: string;
}

const PatientForm: React.FC = () => {
  const [submittedData, setSubmittedData] = useState<Omit<
    PatientFormValues,
    'hasTaxId' | 'hasMiddleName'
  > | null>(null);

  const onSubmit = (values: PatientFormValues) => {
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    const { hasTaxId, hasMiddleName, ...filteredValues } = values;
    setSubmittedData(filteredValues);
  };

  return (
    <div className="p-8 bg-gray-100">
      <h1 className="text-xl font-bold mb-4"> </h1>

      <Form
        onSubmit={onSubmit}
        validate={(values) => validateFormValues(values, validationSchema)}
        render={({ handleSubmit, form, values, submitting }) => (
          <form onSubmit={handleSubmit} className="space-y-6">
            <section className="bg-white p-6 rounded-md shadow">
              <h2 className="text-lg font-semibold mb-4"> </h2>
              <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                <InputField key="lastName" name="lastName" label="*" />

                <InputField key="firstName" name="firstName" label="'*" />

                <InputField
                  key="middleName"
                  optional
                  optionalValue={values && values.hasMiddleName}
                  onOptionalChange={(checked) => {
                    form.change('hasMiddleName', checked);

                    if (checked) {
                      form.resetFieldState('middleName');
                    }
                  }}
                  name="middleName"
                  label=" *"
                  additionalText={
                    !values.hasMiddleName
                      ? '    '
                      : undefined
                  }
                />

                <InputField
                  key="taxId"
                  optional
                  optionalValue={values && values.hasTaxId}
                  onOptionalChange={(checked) => {
                    form.change('hasTaxId', checked);

                    if (checked) {
                      form.resetFieldState('taxId');
                    }
                  }}
                  name="taxId"
                  label=" (I)*"
                  mask="9999999999"
                  additionalText={
                    !values.hasTaxId
                      ? ' I       '
                      : undefined
                  }
                />

                <InputField
                  key="dob"
                  name="dob"
                  label=" *"
                  type="date"
                />

                <SelectField
                  key="gender"
                  name="gender"
                  label="*"
                  options={[
                    { label: '', value: 'male' },
                    { label: '', value: 'female' },
                  ]}
                />
              </div>

              <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mt-4">
                <InputField
                  key="birthCountry"
                  name="birthCountry"
                  label=" *"
                />

                <InputField
                  key="birthPlace"
                  name="birthPlace"
                  label=" *"
                />

                <SelectField
                  key="contactMethod"
                  name="contactMethod"
                  label="  '  "
                  options={[
                    { label: ' ', value: 'email' },
                    { label: '', value: 'phone' },
                  ]}
                />

                <InputField
                  key="secretWord"
                  name="secretWord"
                  label="  (  6 )*"
                />

                <InputField
                  key="phone"
                  name="phone"
                  label="  "
                  mask="+38 (999) 999-99-99"
                  placeholder="+38 (999) 999-99-99"
                />

                <InputField
                  key="email"
                  name="email"
                  label="  "
                  placeholder="example@example.com"
                />
              </div>
            </section>

            <section className="bg-white p-6 rounded-md shadow mt-6">
              <h2 className="text-lg font-semibold mb-4">
                ,   
              </h2>

              <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mt-4">
                <SelectField
                  key="docType"
                  name="docType"
                  label=" *"
                  options={[
                    {
                      label:
                        ' ,    ',
                      value: 'protection_certificate',
                    },
                    { label: ' (ID-)', value: 'id-card' },
                    { label: ' ()', value: 'passport_booklet' },
                    {
                      label: '     ',
                      value: 'permanent_residence_permit',
                    },
                    { label: ' ', value: 'refugee_certificate' },
                    {
                      label: '  ',
                      value: 'residence_permit',
                    },
                    {
                      label: '   ',
                      value: 'temporary_ukrainian_certificate',
                    },
                  ]}
                />

                <InputField
                  key="docNumber"
                  name="docNumber"
                  label=" ( ), *"
                  placeholder="AB123456"
                />

                <InputField
                  key="docIssueDate"
                  name="docIssueDate"
                  label=" *"
                  type="date"
                />

                <InputField
                  key="docExpiryDate"
                  name="docExpiryDate"
                  label=" "
                  type="date"
                />

                <InputField
                  key="docIssuer"
                  name="docIssuer"
                  label=" *"
                  placeholder=",   "
                />

                <InputField
                  key="unrzNumber"
                  name="unrzNumber"
                  label="  ()"
                  placeholder="-"
                  mask="99999999-aaaaa"
                />
              </div>
            </section>

            <div className="flex justify-center mt-6">
              <Button type="submit" className="w-1/2" disabled={submitting}>
                
              </Button>
            </div>
          </form>
        )}
      />

      {submittedData && (
        <div className="mt-6 p-4 bg-white rounded-md shadow">
          <h2 className="text-lg font-semibold mb-2">
                JSON:
          </h2>
          <pre className="text-sm bg-gray-100 p-2 rounded overflow-x-auto">
            {JSON.stringify(submittedData, null, 2)}
          </pre>
        </div>
      )}
    </div>
  );
};

export default PatientForm;
